/*=======Barra de Navegação========*/

.img-logo{

    height: 40px;
    width: 132px;
   background: url(/Portifolio/static/media/logo_empresa.d2355e1f.PNG) no-repeat;
    
    display: block;
    
    background-size: contain;

    color: transparent;
    
    }

    nav.navbar-transparente {
        padding: 15px 0px;
        background: rgba(0,0,0,0.4);
        }
        
        .navbar-brand{
        
        padding: 5px 15px;
        
        }


            #barra-navegacao ul li{ 
                display: inline; 
                margin: 10px;               
            }

            #barra-navegacao ul li a{ 
                color:white;  
                text-decoration: none             
            }
            #barra-navegacao ul li a:hover{ 
                color: pink;               
            }

            
